import React, { Component } from 'react';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import 'bootstrap/dist/css/bootstrap.min.css';
import {NavLink, Router} from 'react-router-dom';


class NavigationBar extends Component {
    render() {
        return (
            <div>
                    <Navbar fixed="top" bg="dark" variant="dark">
                    <Navbar.Brand href="#home"><i>MUDRA</i>converter</Navbar.Brand>
                        <Nav fill variant="tabs" defaultActiveKey="/" activeKey={{color:"green"}} >
                        <Nav.Item>
                            <Nav.Link as={NavLink} activeStyle={{color:'green'}} exact to="/" eventKey="link-1">
                                Introduction
                            </Nav.Link>
                        </Nav.Item>
                        <Nav.Item>
                            <Nav.Link as={NavLink} activeStyle={{color:'green'}} exact to="/converter" eventKey="link-2">
                                Converter
                            </Nav.Link>
                        </Nav.Item>
                        <Nav.Item>
                            <Nav.Link as={NavLink} activeStyle={{color:'green'}} exact to="/developer" eventKey="link-3">
                                Developer
                            </Nav.Link>
                        </Nav.Item>
                        </Nav>
                    </Navbar>
            </div>
        );
    }
}

export default NavigationBar;