import React, { Component, } from 'react';
import { Form } from 'react-bootstrap';
import NavigationBar from './NavigationBar';
import axios from 'axios';
import Helmet from 'react-helmet';

class Converter extends Component {

    constructor(props){
        super(props);
        this.state={
            myData:[],
            from:'Afghan afghani',
            fromCode:'AFN',
            to:'Afghan afghani',
            toCode:'AFN',
            amount:'1',
            answar:'1',
        }
    }
    componentDidMount(){
        axios.get('https://restcountries.eu/rest/v2/all')
            .then(response=>{
                this.setState({myData:response.data})
            })
            .catch(error=>{
                console.log(error)
            })
    }

    changeFrom=(event)=>{
        var targetValue = event.target.value;
        this.setState({
            from:targetValue
        })
        var myList = this.state.myData;
        var CurrencyCodeFrom = myList.map((myList)=>{
            var cur = myList.currencies[0].name;
            if(cur === targetValue){
                this.setState({
                    fromCode:myList.currencies[0].code
                })
                return myList.currencies[0].code
            }
        })
    }
    changeTo=(event)=>{
        var targetValue = event.target.value;
        this.setState({
            to:targetValue
        })
        var myList = this.state.myData;
        var CurrencyCodeTo = myList.map((myList)=>{
            var cur = myList.currencies[0].name;
            if(cur === targetValue){
                this.setState({
                    toCode:myList.currencies[0].code
                })
                return myList.currencies[0].code
            }
        })
    }
    changeAmount=(event)=>{
        var targetValue = event.target.value;
        this.setState({
            amount:targetValue
        })
    }
    convert=()=>{
        axios.get('https://free.currconv.com/api/v7/convert?q='+ this.state.fromCode+'_'+ this.state.toCode +'&compact=ultra&apiKey=2a6e3e82d159e471b9a8')
        .then(answa=>{
            var hh = this.state.fromCode+'_'+ this.state.toCode
            var answar = answa.data[hh] * this.state.amount;
            this.setState({
                answar:answar
            })

        })
        .catch(error=>{
            alert("An error occured")
        })

    }

    render() {

        const myList = this.state.myData;
        const CurrencyName = myList.map((myList)=>{
            return <option>{myList.currencies[0].name}</option>
        })

        const CurrencySymbol = myList.map((myList)=>{
            var cur = myList.currencies[0].name;
            if(cur === this.state.from){
                return <b style={{fontSize:32 + 'px',color:'#E342ff'}}>{myList.currencies[0].symbol}</b>
            }
        })

        const RequiredCurrencySymbol = myList.map((myList)=>{
            var rCur = myList.currencies[0].name;
            if(rCur === this.state.to){
                return <b style={{fontSize:32 + 'px',color:'Yellow'}}>{myList.currencies[0].symbol}</b>
            }
        })

        return (
            <div>
                <Helmet>
                    <title>Converter | {this.state.from} to {this.state.to}</title>
                </Helmet>
                <div style={{position:'absolute',top:58 + 'px'}}>
                    <Form inline>
                        <Form.Group controlId="exampleForm.SelectCustomSizeLg">
                            <Form.Label style={{fontSize:32 + 'px',color:'#61dafb'}}><b>From</b></Form.Label>
                            <Form.Control onChange={this.changeFrom} name="from" as="select" size="lg" custom>
                                {CurrencyName}
                            </Form.Control>
                            <b style={{fontSize:32 + 'px',color:'#61dafb'}}>&#8594;</b>
                            <Form.Label style={{fontSize:32 + 'px',color:'#61daaa'}}><b>To</b></Form.Label>
                            <Form.Control onChange={this.changeTo} name="to" as="select" size="lg" custom>
                                {CurrencyName}
                            </Form.Control>
                        </Form.Group>
                    </Form>
                    <br></br><br></br><br></br>
                    <Form inline className="mt-5">
                        <Form.Label style={{fontSize:32 + 'px',color:'#E342ff'}}><b>Enter the Amount:</b></Form.Label>
                        <Form.Control onChange={this.changeAmount} name="amount" type="number" value={this.state.amount} size="lg" custom>
                        </Form.Control>{CurrencySymbol}
                    </Form>
                    <input type="button" onClick={this.convert} value="Convert"></input><br></br>
                    <span style={{fontSize:32 + 'px',color:'yellow'}}>{this.state.amount}  {this.state.from}={this.state.answar} {RequiredCurrencySymbol}({this.state.to})</span>
                </div>
            </div>
        );
    }
}

export default Converter;