import React, { Component } from 'react';
import Aos from 'aos';
import 'aos/dist/aos.css';
import NavigationBar from './NavigationBar';
import Helmet from 'react-helmet';
import 'bootstrap/dist/css/bootstrap.min.css';

class Introduction extends Component {
    constructor(props){
        super(props);
      }
      componentDidMount(){
        Aos.init({
          duration : 2000
        })
      }
    render() {
        return (
            <div>
                <Helmet>
                    <title>Introduction | MUDRAConverter</title>
                </Helmet>
                <img style={{width:100 + '%',height:'calc(100vh - 114px'}} src="https://www.picpedia.org/highway-signs/images/currency-exchange.jpg" alt="welcome_image"></img>
                <div>
                    <h1 style={{position:'absolute',top:'60'+'vh',color:'darkblue'}}>Welcome to <i>MUDRA</i>converter<b style={{marginLeft:50 + 'px'}}>&#8595;</b></h1>
                </div>
                <div class="container mt-4 mb-5">
                    <div data-aos="zoom-in">
                        <h2 style={{color:'#61dafb'}}>Objective</h2>
                        <span style={{fontSize:32 + 'px',color:'#E34234'}}>This application is developed for converting currencies.</span>
                        <span style={{fontSize:22 + 'px',color:'#E342ff'}}>We developed this application for educational purposes only.We don't have any buiseness goal</span><br></br>
                        <div className="mt-4">
                            <h4 style={{color:'wheat'}}>Specialities--</h4>
                            <ul>
                                <li style={{color:'yellow'}}><b>Free to use</b></li>
                                <li style={{color:'yellow'}}><b>No login required</b></li>
                            </ul>
                        </div>
                    </div>
                    <div className="mt-5">
                        <h2 style={{color:'#61dafb'}}>Credits</h2>
                        <span>We use an third party api from <a href="https://currencyfreaks.com/">CurrencyFreaks</a></span>
                    </div>
                </div>
            </div>
        );
    }
}

export default Introduction;